html {
  font-size: 62.5%;
  box-sizing: border-box;
  overflow-x: hidden;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #111316;
  overflow-x: hidden;
}

p {
  color: 900;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** Media Queries **/
.blue-buttom-block {
  background-color: #5193be;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2.5rem;
  text-decoration: none;
  text-transform: uppercase;
  padding: 1rem 3rem;
  text-align: center;
  border: 0.5rem double #262c3a;
  border-radius: 1rem;
}
.blue-buttom-block:hover {
  background-color: #4387b3;
  cursor: pointer;
}

.blue-buttom {
  background-color: #5193be;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2.5rem;
  text-decoration: none;
  text-transform: uppercase;
  padding: 1rem 3rem;
  text-align: center;
  border: 0.5rem double #262c3a;
  border-radius: 1rem;
}
.blue-buttom:hover {
  background-color: #4387b3;
  cursor: pointer;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.container-center {
  max-width: 80rem;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.align-center {
  display: flex;
  justify-content: center;
}

.w-100 {
  width: 100%;
}

.h-50 {
  height: 5rem;
}

.h-100-cover {
  height: 100%;
  object-fit: cover;
}

.relative {
  position: relative;
}

.separation {
  margin: 3rem 0 3rem 0;
  border-top: 1px solid #FFFFFF;
}

.hidden {
  display: none;
}

.header {
  background-color: #262c3a;
  padding: 1rem 0 3rem 0;
}

@media (min-width: 768px) {
  .bar {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 12rem;
  }
}
.bar .image-container {
  width: auto;
}
@media (min-width: 768px) {
  .bar .image-container img {
    max-width: 35rem;
    max-height: 15rem;
  }
}

.searcher {
  margin: 3rem 0;
}
.searcher h1 {
  color: #FFFFFF;
  font-weight: 700;
  margin-top: 2rem;
}
.searcher input {
  padding: 1rem;
  width: 100%;
  background-color: #FFFFFF;
  border: 3px solid #4141c0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .searcher input {
    width: 40%;
  }
}
.searcher input:focus {
  outline: none;
}

.mobile-filters {
  background-color: #3c3e44;
  padding: 1rem;
  border-radius: 1rem;
  margin-top: 1rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .mobile-filters {
    display: none;
  }
}
.mobile-filters .filter-title {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.mobile-filters .filter-title p {
  color: #FFFFFF;
  font-weight: 700;
  font-size: 2.8rem;
  margin: 0;
}
.mobile-filters .filter-title img {
  width: 15%;
}

.icon-filter {
  filter: invert(100%);
}
.icon-filter.ico-filter-width {
  width: 5%;
}

.container-sections {
  background-color: #272b33;
  border-radius: 1rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .container-sections {
    display: flex;
    gap: 4rem;
    height: 60rem;
  }
}

.results-container {
  padding: 1.5rem;
}
@media (min-width: 768px) {
  .results-container {
    flex: 0 0 60%;
    height: 54rem;
    overflow-y: auto;
    margin-top: 2rem;
    padding: 0 0.5rem 0 2rem;
  }
}
.results-container .buttons-container {
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .results-container .buttons-container {
    display: flex;
    justify-content: space-evenly;
    gap: 3rem;
  }
}
.results-container .buttons-container button {
  width: 100%;
}
.results-container .buttons-container button:first-of-type {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .results-container .buttons-container button {
    width: 35%;
  }
  .results-container .buttons-container button:first-of-type {
    margin: 0;
  }
}

.result {
  background-color: #3c3e44;
  margin-bottom: 2rem;
  border: 2px solid #4141c0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .result {
    display: flex;
  }
}
.result:last-of-type {
  margin: 0;
}
@media (min-width: 768px) {
  .result .image-result-container {
    flex: 0 0 25rem;
  }
}
.result .image-result-container img {
  border-top-right-radius: 1rem;
  border-top-left-radius: 1rem;
}
@media (min-width: 768px) {
  .result .image-result-container img {
    border-top-right-radius: 0;
    border-bottom-left-radius: 1rem;
  }
}
.result .features-result-container {
  padding: 0 1.5rem;
}
@media (min-width: 768px) {
  .result .features-result-container {
    flex: 1;
  }
}

.result-info p, .result-info h3 {
  margin: 0;
}
.result-info h3 {
  color: #FFFFFF;
  font-weight: 900;
}
.result-info p {
  color: #FFFFFF;
}
.result-info p span {
  font-weight: 700;
  text-shadow: 1px 1px 2px #000000;
}
.result-info .text-gray {
  color: #9e9e9e;
  font-size: 1.6rem;
}
.result-info .info {
  font-size: 1.8rem;
}

.message {
  color: #FFFFFF;
  font-weight: 700;
}
.message span {
  font-weight: 900;
}

.filters {
  position: absolute;
  right: -100rem;
  top: 0;
  width: 100%;
  background-color: #111316;
}
.filters.static {
  position: static;
}
@media (min-width: 768px) {
  .filters {
    position: static;
    flex: 1rem;
    background-color: transparent;
    height: 35rem;
    overflow-y: auto;
    margin-top: 4rem;
  }
}
.filters .filter-close-mobile-menu {
  margin: 2rem;
}
@media (min-width: 768px) {
  .filters .filter-close-mobile-menu {
    display: none;
  }
}
.filters .filter-close-mobile-menu div {
  background-color: #3c3e44;
  width: 5rem;
  height: 5rem;
  border-radius: 100%;
  cursor: pointer;
}
.filters .filter-close-mobile-menu div img {
  width: 50%;
}
.filters .filter-title::first-letter {
  text-transform: uppercase;
}

.filters-container {
  padding: 2rem;
}
@media (min-width: 768px) {
  .filters-container {
    padding: 0 0.5rem 0 0;
  }
}
.filters-container .filter {
  background-color: #3c3e44;
  padding: 1rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
}
.filters-container .filter:last-of-type {
  margin: 0;
}

.filter-clear {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.filter-clear p {
  color: #FFFFFF;
  margin: 0;
  font-size: 1.4rem;
}
.filter-clear p:first-of-type {
  font-size: 2.8rem;
  font-weight: 700;
}
.filter-clear p:last-of-type {
  cursor: pointer;
  text-decoration: underline;
}

.filter-search p {
  color: #FFFFFF;
  margin: 0 0 1rem 0;
  font-size: 2.3rem;
}
.filter-search .filter-search-options {
  margin: 0;
  padding-left: 1rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.filter-search .filter-search-options li {
  color: #FFFFFF;
  list-style: none;
  background-color: #1c1f25;
  padding: 1rem;
  border-radius: 1rem;
  text-align: center;
  cursor: pointer;
  width: auto;
}
.filter-search .filter-search-options li.selected {
  color: #000000;
  background-color: #9b9b9b;
}
@media (min-width: 768px) {
  .filter-search .filter-search-options li {
    width: 40%;
  }
}

.footer {
  background-color: #262c3a;
  padding: 2.5rem;
}
.footer p {
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  margin: 0;
}

.info-footer {
  margin-bottom: 3rem;
}
.info-footer p {
  color: #9e9e9e;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.6rem;
}